<template>
    <div class="stats-distribution">
        <h1>Distribute statistic points</h1>
        <div class="stat-description">
            <p>Remaining points: <span class="highlight">{{pointsToAllocate}}</span></p>
            
        </div>
        <div class="statistics-list">
            <div class="stat" v-for="(stat, index) in statistics" :key="index">
                <div class="stat-name int-stat "><span class="highlight" @click="openEncyclopedia('abilities', stat.name)"> {{stat.name}}</span>: {{stat.value}}</div>
                <div class="int-increase stat-manip" @click="increaseStat(stat.name)"><span>+</span></div>
                <div class="int-decrease stat-manip" @click="decreaseStat(stat.name)"><span>-</span></div>
                
            </div>
        </div>
        <div class="buttons-wrap">
            <div class="apply-button btn" @click="acceptStats">ACCEPT</div>
            <div class="back-button btn" @click="cancelStats">CANCEL</div>
        </div>
        <!-- encyclopedia -->
        <encyclopedia v-if="encyclopediaOpen" @closeEncyclopedia="encyclopediaOpen=false" :category="encyclopediaCategory" :entry="encyClopediaEntry"></encyclopedia>
    </div>
</template>

<script>
import encyclopedia from './encyclopedia.vue'
export default {
    components: {
        encyclopedia
    },
    data() {
        return{
            statistics: [{name: 'Vitality', value: 1}, {name: 'Strength', value: 1}, {name:'Dexterity', value: 1}, {name: 'Intelligence', value: 1}, {name:'Spirit', value: 1}, {name:'Luck', value: 1}],
            pointsToAllocate: 5,
            encyclopediaOpen: false,
            encyclopediaCategory: null,
            encyClopediaEntry: null,
        }
    },
    methods: {
        openEncyclopedia(category, entry) {
            
            this.encyClopediaEntry = entry;
            this.encyclopediaCategory = category;
            this.encyclopediaOpen=true;
        },
        acceptStats() {
            this.$emit('closeStats');
        },
        cancelStats() {
            this.$emit('closeStats');            
        },
        increaseStat(stat) {
            if (this.pointsToAllocate > 0) {
                    this.pointsToAllocate --;
                switch (stat) {
                    case "Vitality":
                    this.statistics[0].value ++;
                    break;

                    case "Strength":
                    this.statistics[1].value ++;
                    break;

                    case "Dexterity":
                    this.statistics[2].value ++;
                    break;

                    case "Intelligence":
                    this.statistics[3].value ++;
                    break;

                    case "Spirit":
                    this.statistics[4].value ++;
                    break;

                    case "Luck":
                    this.statistics[5].value ++;
                    break;
                }
            } else {
                return;
            }
            
        },
        decreaseStat(stat) {
                switch (stat) {
                    case "Vitality":
                    if(this.statistics[0].value <=1) {
                        break;
                    } else {
                        this.statistics[0].value --;
                        this.pointsToAllocate ++;
                    }                    
                    break;

                    case "Strength":
                     if(this.statistics[1].value <=1) {
                        break;
                    } else {
                        this.statistics[1].value --;
                        this.pointsToAllocate ++;
                    }
                    break;

                    case "Dexterity":
                     if(this.statistics[2].value <=1) {
                        break;
                    } else {
                        this.statistics[2].value --;
                        this.pointsToAllocate ++;
                    }
                    break;

                    case "Intelligence":
                     if(this.statistics[3].value <=1) {
                        break;
                    } else {
                        this.statistics[3].value --;
                        this.pointsToAllocate ++;
                    }
                    break;

                    case "Spirit":
                     if(this.statistics[4].value <=1) {
                        break;
                    } else {
                        this.statistics[4].value --;
                        this.pointsToAllocate ++;
                    }
                    break;

                    case "Luck":
                     if(this.statistics[5].value <=1) {
                        break;
                    } else {
                        this.statistics[5].value --;
                        this.pointsToAllocate ++;
                    }
                    break;
                }
            
            
        }

    }
}
</script>

<style scoped>
.stats-distribution {
    background: #000408;
    color: #BCE0D6;
    height: 100vh;
    width: 100%;
    display: grid;
    position: absolute;
    justify-content: center;
    align-content: center;
    }

.stats-distribution h1 {
    margin-bottom: 1rem;
}

.stats-distribution p{
        font-size: 1.5rem;
        line-height: 1.8rem;
        margin-bottom: .5rem;
    }

.statistics-list {
    display: grid;
    overflow-y: scroll;
}

.stat {
    display: grid;
    grid-template-columns: 3fr 1fr 1fr;
    grid-gap: 1rem;
    margin-bottom: 1rem;
}

.stat-manip {
    border: 1px solid #BCE0D6;
    padding: .3rem;
    text-align: center;
}

.stat-name {
    font-size: 1.8rem;
    line-height: 2.2rem;
    padding: .5rem;
}
.stat-manip span {
    font-size: 2.4rem;
    
}


</style>
