<template>
    <div class="abilities-distribution-component">
        <h1>Choose abilities</h1>
        <div class="ability-points-display">Points to allocate: <span class="highlight">0</span></div>
        <div class="abilities-display">
            <div class="class-abilities-wrap" v-if="classAbilitiesPresent">
                <div class="ability general-ability" v-for="(ability, index) in abilities" :key='index'>
                    <p class="ability-name">{{ability.name}}</p>
                    <p class="ability-description">{{ability.desc}}</p>
                    <p class="effect-description highlight">{{ability.effectDesc}}</p>
                </div>
                
                <div class="class-ability ability" v-for="(ability, index) in classAbilities" :key='"2nd"+index' >
                    <p class="ability-name">{{ability.name}}</p>
                    <p class="ability-description">{{ability.desc}}</p>
                    <p class="effect-description highlight">{{ability.effectDesc}}</p>
                    
                </div>
            </div>
            <div class="no-class-selected" v-if="!classAbilitiesPresent">
                Select a character class to choose abilities
            </div>
            
           
        </div>
        <div class="selected-abilities">
           <p class="character-effects-title">Character effects:</p>
           <div class="effects-wrapper" v-for="(effect, index) in effects" :key="index">
               <p class="highlight">{{effect}}</p>
           </div>
        </div>
        <div class="buttons-wrap">
            <div class="btn-close btn" @click="closeAccept">ACCEPT</div>
            <div class="btn-close btn" @click="closeCancel">CANCEL</div>
        </div>
        
        
    </div>
</template>

<script>
export default {
    data() {
        return {
            classAbilities: this.$store.state.abilitySets[this.$store.state.playerCharacter.abilitiesPreset].abilities,
            abilities: this.$store.state.abilities,
            effects: ["effect one", "effect two"]
        }
    },
    methods: {
        closeCancel() {
            this.$emit('closeAbilities')
        },
        closeAccept(){
            
            //check to add points to player stats according to skills
            //for instance check if a given skill is already taken and add hp to player.additionalHP
        }
    },
    computed: {
        classAbilitiesPresent() {
            if (this.$store.state.playerCharacter.abilitiesPreset == null) {
                return false
            } else { return true}
        }
    }
}
</script>

<style scoped>
.abilities-distribution-component{
    background: #000408;
    color: #BCE0D6;
    height: 100vh;
    width: 100%;
    display: grid;
    position: absolute;
    justify-content: center;
    align-content: center;
    }
    .abilities-distribution-component h1 {
        text-align: center;
        margin-bottom: 1rem;
    }

    .ability-points-display {
        font-size: 1.4rem;
        line-height: 1.8rem;
        margin-bottom: 1rem;
    }

    .abilities-display {
        margin-bottom: 1rem;
    }

    .abilities-wrap {
        overflow-y: scroll;
        display: grid;
        
    }

    .ability {
        border: 1px solid #BCE0D6;
        padding: 1rem;
        margin: .5rem 1rem;
        width: 300px;
        max-width: 300px;
        transition: .4s all ease-in-out;
        cursor: pointer;
    }

    .ability-name {
        font-size: 1.4rem;
        line-height: 1.8rem;
    }

    .ability-description {
        line-height: 1.4rem;
    }

    .effect-description {
        line-height: 1.4rem;
    }

    .selected-ability {
        border: 1px solid #AC7C0A;
        
    }

    .selected-abilities {
        margin-bottom: 1rem;
    }

    @media(min-width: 400px ) {
        .ability:hover {
        border: 1px solid #AC7C0A;
        }
    }
</style>
