<template>
  <div class="character-creation-module">
    <div class="character-sheet">
      <p>character name: {{playerCharacter.charName}}</p>
      <p>character class: {{playerCharacter.class}}</p>
      <p v-if="charBackground">Character's Raison D'etre: {{charBackground.reasonNameDisplay}} ({{charBackground.effect}})</p>
      
    </div>
    <div class="creation-list-wrap">
      <ul class="creation-list">
        <li @click="nameOpen=true">NAME</li>
        <li @click="classOpen=true">CLASS</li>
        <li @click="statsOpen=true">STATISTICS</li>
        <li @click="openAbilitiesFn">ABILITIES</li>
        <li @click="backgroundOpen=true">BACKGROUND</li>
        
      </ul>
      <div class="buttons-wrap-list">
        <div class="btn"><router-link to="/">BACK</router-link></div>
        <div class="btn"><router-link to="/gameSection">ACCEPT</router-link></div>
      </div>
    </div>
    
    
    <!-- name input -->
    <div class="name-section slide-modal" v-if="nameOpen">
      <div class="modal-content">
      <h1>Choose a name</h1>
        
        <div class="name-input">Your name:</div>
      <input type="text" v-model="playerCharacter.charName">
      <div class="select-portrait">
        portrait here and btns to change it
      </div>
      <div class="buttons-wrap">
        <div class="back-button btn" @click="nameOpen=false"> DONE</div>
      </div>
      
      </div>
    </div>
    <!-- class select -->
    <div class="class-select slide-modal" v-if="classOpen">
        <div class="modal-content class-modal">
      <h1 class="modal-title">Character class</h1>
          
          <div class="class-select-section">
            <div class="select-engineer select-class" @click="selectClass('engineer', 0)" :class="{'selected':classSelected=='engineer'}">Engineer</div>
            <div class="select-hunter select-class" @click="selectClass('hunter', 1)" :class="{'selected':classSelected=='hunter'}">Hunter</div>
            <div class="select-engineer select-class" @click="selectClass('scholar', 2)" :class="{'selected':classSelected=='scholar'}">Scholar</div>
            
            
          </div>
          <div class="class-description-section">
            <p>{{classDescs[classSelected + 'desc']}}</p>
          </div>
          <div class="buttons-wrap-list">
            <div class="apply-button btn" @click="acceptClass">ACCEPT</div>
            <div class="back-button btn" @click="classOpen=false">CANCEL</div>
          </div>
        
      </div>
    </div>
    <!-- background selection -->
    <background-select v-if="backgroundOpen" @backgroundClose='backgroundOpen=false' @backgroundCloseApply="charBackground=$event; backgroundOpen=false"></background-select>
    <!-- abilities distribution -->
    <abilities-distribution v-if="abilitiesOpen" @closeAbilities="abilitiesOpen=false"></abilities-distribution>
    <!-- stat distribution -->
    <stat-distribution v-if="statsOpen" @closeStats="statsOpen=false"></stat-distribution>
    <!-- info modal -->
    <transition name="show-info-modal">
      <info-modal v-if="showModal" :msg="infoMsg"></info-modal>
      </transition>
  </div>
</template>

<script>
// @ is an alias to /src
import abilitiesDistribution from '../components/abilitiesDistribution.vue'
import infoModal from '../components/infoModal.vue'
import statDistribution from '../components/statDistribution.vue'
import backgroundSelect from '../components/backgroundSelect.vue'

export default {
  name: 'characterCreation',
  components: {
    abilitiesDistribution,
    infoModal,
    statDistribution,
    backgroundSelect,
  },
  data() {
    return {
      showModal: false,
      infoMsg: '',
      nameOpen: false,
      classOpen: false,
      statsOpen: false,
      abilitiesOpen: false,
      backgroundOpen: false,
      classSelected: '',

      charBackground: null,

      classDescs: {
        desc: 'select a class',
        hunterdesc: 'a hunter class, has traps and shit',
        engineerdesc: 'an engineer class, has an armor and crafts stuff',
        scholardesc: 'class currently not available'
      },

      playerCharacter: {
        charName: '',
        class: ''
      }
    }
  },
  methods: {
    selectClass(charClass, charNum) {
      this.classSelected = charClass;
      this.playerCharacter.class = charClass;
      this.$store.state.playerCharacter.abilitiesPreset = charNum;
      
    },
    acceptClass(charClass) {
      this.classOpen = false;
      
      // acceptClass(classSelected)
    },
    showModalFn(msg) {
      this.infoMsg = msg;
      this.showModal = true;
      setTimeout ( () => {
        this.showModal = false;
        }, 1000)
    },
    openAbilitiesFn() {
      
      if(this.$store.state.playerCharacter.abilitiesPreset === null) {
        this.showModalFn('Please select a character class before choosing abilities')
      } else {
        this.abilitiesOpen=true;
      }
    }
  }
}
</script>

